@import "https://fonts.googleapis.com/css2?family=Orbitron&display=swap";
body {
  width: 100%;
  height: 100vh;
  background-color: #000;
  font-family: Orbitron, sans-serif;
  overflow: hidden;
}

.text-wrapper {
  color: bisque;
  justify-content: center;
  gap: 130px;
  font-size: 20px;
  display: flex;
}

.content-wrapper {
  justify-content: center;
  align-items: center;
  display: flex;
  position: relative;
}

.img-morfiys {
  width: 650px;
  height: 600px;
  background-image: url("morfius.bg.08ad5fea.png");
  background-position: center;
  background-repeat: no-repeat;
  margin: 0 auto;
}

.palm-red, .palm-blue {
  opacity: 0;
  position: absolute;
}

.image-opacity {
  opacity: 1;
}

.js-start {
  position: fixed;
  bottom: 40px;
  left: 30%;
}

.js-close {
  position: fixed;
  bottom: 40px;
  right: 28%;
}

button {
  cursor: pointer;
  height: 190px;
  width: 190px;
  background-color: rgba(0, 0, 0, 0);
  border: none;
  padding: 80px;
}

.red {
  color: red;
}

.blue {
  color: #00f;
}

.back {
  position: absolute;
  top: 5px;
  left: 20px;
}

.back a {
  color: azure;
  font-size: 20px;
  text-decoration: none;
}

.back a:hover {
  color: wheat;
}

